*{
  margin: 0;
}

.app{
  background-color: #fafafa;
}

.app__posts{
  padding: 20px;
  display: flex;
  justify-content: center;
}

.app__postsRight {
  margin-left: 20px;
}

.app__header{
  position: sticky;
  top:0;
  z-index: 1;
  background-color: white;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  object-fit: contain;
  display: flex;
  justify-content: space-between;
}

.app__headerImage{
  object-fit: contain;
}
.app__signUp{
  display: flex;
  flex-direction: column;
}

